function positionObj() {
  var scrollY = window.scrollY || window.pageYOffset;
  var maxScrollY = document.documentElement.scrollHeight - window.innerHeight;
  var path = document.getElementById("map");
  var pathLen = path.getTotalLength();
  var dist = (pathLen * scrollY / maxScrollY);
  var pos = path.getPointAtLength(dist);
  if (dist + 1 <= pathLen) {
    var posAhead = path.getPointAtLength(dist + 250);
	var angle = Math.atan2(posAhead.y - pos.y, posAhead.x - pos.x);

  } else {
    var posBehind = path.getPointAtLength(dist - 1);
	var angle = Math.atan2(pos.y - posBehind.y, pos.x - posBehind.x);
  }

  var img = document.querySelector('#obj');

  $(window).bind('mousewheel DOMMouseScroll MozMousePixelScroll', function(event) {
	delta = parseInt(event.originalEvent.wheelDelta || -event.originalEvent.detail);
    if (delta > 0) {
		$('#obj').attr('href', './img/svg/bg/object_scale.svg');
	} else {
		$('#obj').attr('href', './img/svg/bg/object.svg');
	}
	
  });

	img.setAttribute("transform", "translate(" +  pos.x + "," + pos.y  + ") rotate(" + rad2deg(angle) + ")");

}

function rad2deg(rad) {
  return -60 + 180 * rad / Math.PI;
}

window.addEventListener("scroll", positionObj);

positionObj();

function nextMsg(i) {
    if (messages.length == i) {
        i = 0;
    }
    $(".wrap_search__form span").html(messages[i]).fadeIn(100).delay(7000).fadeOut(100, function() {
        nextMsg(i + 1);
    });
};

