		if(!$('.smart_push').hasClass('smart_push-active')) {
			$('.smart_push').css('display', '');
		};

		if(windowWidth < 485) {
			$('.smart_push').addClass('smart_push-active');
			element.addClass('changeSmart');
			var imageSmart = element.find('.slider_item > .insert').attr('data-img-smart');
			element.css('background-image', 'url(' + imageSmart + ')');
		}
