var slider_wrap2 = $('#slider2 .wrap_slider-card__items');

slider_wrap2.slick({
	slidesToShow: 4,
	slidesToScroll: 1,
	infinite: false,
	arrows: false,
	dots: false,
	draggable: false,
	TouchMove: false,
	// swipe: false,
	responsive: [
		{
			breakpoint: 991,
			settings: {
				slidesToShow: 2,
				slidesToScroll: 1,
			}
		}
	]
});

$('#slider2 .card__nav_btn-left').on('click', function() {
	slider_wrap2.slick('slickPrev');
});

$('#slider2 .card__nav_btn-right').on('click', function() {
	slider_wrap2.slick('slickNext');
});

var parallax_x1 = document.querySelector('#parallax_x1');
var parallax = new Parallax(parallax_x1);